body {
  text-align: center;
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px;
  font-family: Arial, sans-serif;
  display: flex;
}

h1 {
  color: #1215ba;
}

.loader, .error {
  color: red;
  font-size: 18px;
  display: none;
}

.select-wrapper {
  margin-top: 30px;
}

.cat-info {
  text-align: left;
  border: 1px solid #aee90b;
  margin-top: 20px;
  padding: 10px;
  display: none;
}

.cat-info img {
  max-width: 100%;
  height: auto;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-top: 4px solid #fff;
  border-right: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 48px;
  height: 48px;
  border-bottom: 4px solid #ff3d00;
  border-left: 4px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

